import { Injectable } from '@angular/core'
import { CanActivate, Router } from '@angular/router'
import * as crypto from 'crypto-js'

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {

  constructor(
    public router: Router
  ) { }

  canActivate() {
    if (localStorage.getItem('cva')) {
      var bytes = crypto.AES.decrypt(localStorage.getItem('cva'), 'WilkaETruck2019')
      var decryptProfile = bytes.toString(crypto.enc.Utf8)
      var currentUser = JSON.parse(decryptProfile)
      if (currentUser != null) {
        return true
      } else {
        this.router.navigate(['/login'])
        return false
      }
    } else if (sessionStorage.getItem('cva')) {
      var bytes = crypto.AES.decrypt(sessionStorage.getItem('cva'), 'WilkaETruck2019')
      var decryptProfile = bytes.toString(crypto.enc.Utf8)
      var currentUser = JSON.parse(decryptProfile)
      if (currentUser != null) {
        return true
      } else {
        this.router.navigate(['/login'])
        return false
      }
    }
    this.router.navigate(['/login'])
    return false
  }
}
